@import "../mixins.scss";


$iconos: #fa2, #0e76a8, #4FCE5D;

//piano

section#seccion-1.piano {
    @include t(100%,100vh);
    div.principal {
        @include t(100%,100%);
        position: relative;
        @include centrar(h);
        h2 {
            width: 100%;
            text-align: center;
            border-bottom: 10px solid $piano1;
        }
        ul {
            bottom: 0;
            display: flex;
            margin-top: 30px;
            li {
                list-style: none;
                a {
                    font-size: 60px;
                    color: $piano1;
                }
            }
        }
        p {
            bottom: 0;
            margin-top: 30px;
            font-size: 40px;
            padding-bottom: 10px;
            text-align: center;
        }
    }
}
@media (orientation:landscape) {
    section#seccion-1.piano {
        padding: 90px 10% 30px 10%;
        div.principal {
            h2 {
                font-size: 3em;
                padding: 0 10% 10vh 10%;
            }
            ul {
                position: absolute;
                width: 35%;
                justify-content: space-between;
            }
            p {
                position: absolute;
                right: 0;
                width: 40%;
            }
        }
    }
}
@media (orientation:portrait) {
    section#seccion-1.piano {
        padding: 90px 5% 30px 5%;
        div.principal {
            display: block;
            h2 {
                font-size: 2em;
                padding: 0 0 10vh 0;
            }
            ul {
                width: 100%;
                justify-content: space-around;
            }
            p {
                width: 100%;
            }
        }
    }
}




//toxic

section#seccion-1.toxic {
    height: 100vh;
    position: relative;
    @include toxicfont;
    div.principal {
        max-width: 100%;
        h2 {
            color: #fff;
            width: 100%;
        }
        ul.contactos {
            width: 100%;
            padding-top: 30px;
            padding-bottom: 15px;
            display: flex;
            justify-content: space-around;
            li {
                @include t(80px,80px);
                @include centrar(vh);
                cursor: pointer;
                position: relative;
                list-style: none;
                & * {
                    font-size: 1.5em;
                }
            }
            @for $i from 1 to 4 {
                li:nth-child(#{$i}) {
                    &::after, &::before {
                        content: '';
                        position: absolute;
                        @include t(100%,100%);
                        border: 5px solid nth($iconos, $i);
                    }
                    &:hover {
                        filter: drop-shadow(0 0 20px nth($iconos,$i));
                        &::before {
                            animation: linear animacion 5s infinite;
                        }
                        &::after {
                            animation: linear animacion 4s infinite;
                        }
                    }
                    a {
                        color: nth($iconos,$i);
                    }
                }
            }
        }
        p {
            display: none;
        }
        div.animacion {
            @include centrar(vh);
            ul {
                @include t(200px,200px);
                position: relative;
                @for $i from 1 to 9 {
                    li.n#{$i} {
                        @include t(100%,100%);
                        @include xy-tl(0,0);
                        list-style: none;
                        transform: rotate(45deg*$i);
                        &::before {
                            @include pseudo(15px,15px);
                            background: transparent;
                            border: 3px solid $toxic2;
                            border-radius: 50%;
                            box-shadow: 0 0 10px $toxic2,
                                        -20px 20px 0 $toxic2,
                                        -20px 20px 10px $toxic2,
                                        20px 20px 0 $toxic2,
                                        20px 20px 10px $toxic2,
                                        20px -20px 0 $toxic2,
                                        20px -20px 10px $toxic2,
                                        -20px -20px 0 $toxic2,
                                        -20px -20px 10px $toxic2;
                            animation: animacion linear 5s infinite;
                            transform-origin: 20px;
                        }
                    }
                }
                &:hover li::before {
                    transform-origin: 200px;
                    box-shadow: 0 0 10px $toxic2,
                                -200px 200px 0 $toxic2,
                                -200px 200px 10px $toxic2,
                                200px 200px 0 $toxic2,
                                200px 200px 10px $toxic2,
                                200px -200px 0 $toxic2,
                                200px -200px 10px $toxic2,
                                -200px -200px 0 $toxic2,
                                -200px -200px 10px $toxic2;
                }
            }
        }
    }
}
@media (orientation:landscape) {
    section#seccion-1.toxic {
        div.principal {
            position: absolute;
            bottom: 20px;
            div.animacion {
                @include t(50%,100vh);
                position: fixed;
                top: 0;
                right: 0;
            }
        }
    }
}
@media (orientation:portrait) {
    section#seccion-1.toxic {
        div.principal {
            @include t(100%,50vh);
            h2 {
                font-size: 2em;
                text-align: right;
                span {
                    font-size: 1.5em;
                }
            }
            ul {
                font-size: 2em;
            }div.animacion {
                @include t(100%,50vh);
            }
        }
    }
}
@media (min-width:1000px) {
    section#seccion-1.toxic h2, section#seccion-1.toxic a {
        font-size: 4em;
    }
}
@media (max-width:1000px) and (orientation:landscape) {
    section#seccion-1.toxic h2, section#seccion-1.toxic a {
        font-size: 3em;
    }
}

@keyframes animacion {
    0%{
        transform: rotate(0deg);
        filter: hue-rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
        filter: hue-rotate(360deg);
    }
}

// neomorfismo

section#seccion-1.neomorfismo {
    @include t(100%,100vh);
    div.principal {
        @include t(100%,100%);
        position: relative;
        @include centrar(h);
        background: $neomorfismo2;
        @include neomorfismo-bs2;
        border-radius: 20px;
        h2 {
            width: 100%;
            font-family: 'Poppins', sans-serif;
            text-align: center;
            color: $neomorfismo2;
            text-shadow: -2px -2px 3px rgba(255,255,255,0.3),
                        2px 2px 3px rgba(0,0,0,0.3);
        }
        ul {
            position: absolute;
            display: flex;
            justify-content: space-around;
            left: 0;
            bottom: 40px;
            width: 100%;
            padding-left: 10%;
            padding-right: 10%;
            list-style-position: insides;
            li {
                list-style: none;
                background: $neomorfismo2;
                @include neomorfismo-bs;
                border-radius: 10px;
                @include centrar(vh);
                a {
                    font-size: 2em;
                    color: $neomorfismo3;
                }
            }
        }
        p {
            display: none;
        }
    }
}
@media (orientation:landscape) {
    section#seccion-1.neomorfismo {
        padding: 105px 15% 30px 15%;
        div.principal {
            padding: 50px;
            h2 {
                font-size: 3em;
                padding: 0 10% 10vh 10%;
            }
            p {
                position: absolute;
                right: 0;
                width: 40%;
            }
            ul li {
                @include t(70px,70px);
            }
        }
    }
}
@media (orientation:portrait) {
    section#seccion-1.neomorfismo {
        padding: 120px 10% 30px 10%;
        div.principal {
            display: block;
            padding: 30px;
            h2 {
                font-size: 2em;
                padding: 0 0 10vh 0;
            }
            p {
                width: 100%;
            }
            ul li {
                @include t(50px,50px);
            }
        }
    }
}