@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@100&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@200;600;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Russo+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Stint+Ultra+Expanded&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Teko:wght@300;400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@900&display=swap');

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    transition: 0.5s;
}

body {
    width: 100%;
}

@mixin centrar($d) {
    display: flex;
    @if $d==v {
        justify-content: center;
    } @else if $d==h {
        align-items: center;
    } @else {
        justify-content: center;
        align-items: center;
    }
}
@mixin t($w,$h){
    width: $w;
    height: $h;
}
@mixin xy-tl($x,$y) {
    position: absolute;
    top: $x;
    left: $y;
}

@mixin xy-bl($x,$y) {
    position: fixed;
    bottom: $x;
    left: $y;
}

@mixin pseudo($w,$h) {
    content: '';
    @include t($w,$h);
    @include xy-tl(0,0);
}
@mixin skills {
    ul {
        display: grid;
        grid-gap: 10px;
        grid-template: repeat(3,1fr) / repeat(3,1fr);
        padding: 10px;
        li {
            list-style: none;
            @include centrar(vh);
        }
    }
    ul.no-active {
        li {
            @include t(15px,15px);
            position: relative;
            transition: 0.5s;
            & * {
                display: none;
            }
            &::before {
                content: '';
                @include t(10px,10px);
                border-radius: 50%;
            }
        }
    }
    ul.active {
        li {
            @include t(100px,100px);
            font-size: 2.5em;
        }
    }
}

//piano

$piano1: #000;
$piano2: #fff;
$piano3: #aaa;
@mixin borde-piano-a {
    border: 3px solid $piano1;
    border-radius: 50%;
}
@mixin pianofont {
    font-family: 'Raleway', sans-serif;
}

//toxic
$toxic1: #123;
$toxic2: #0ff;
@mixin toxicfont {
    font-family: 'Teko', sans-serif;
}

//neomorfismo

$neomorfismo1: #ddd;
$neomorfismo2: #8bc;
$neomorfismo3: #666;

@mixin neomorfismo-bs {
    box-shadow: -3px -3px 7px rgba(255,255,255,0.3),
        3px 3px 7px rgba(0,0,0,0.3);
}
@mixin neomorfismo-bs2 {
    box-shadow: inset -5px -5px 7px rgba(0,0,0,0.3),
                inset 5px 5px 7px rgba(0,0,0,0.3);
}
@mixin neomorfismo-bsi {
    box-shadow:inset -3px -3px 7px rgba(255,255,255,0.6),
        inset 3px 3px 7px rgba(0,0,0,0.3);
}