@import "../mixins.scss";

.menu {
    cursor: pointer;
}

.menu.piano {
        @include t(60px,60px);
        @include xy-tl(0,50%);
        transform: translateX(-30px);
        background: none;
        border: none;
        div {
            @include t(100%,3px);
            transform: translateY(-7px);
            background: $piano1;
            &::after {
                @include pseudo(100%,3px);
                transform: translateY(14px);
                background: $piano1;
            }
        }
}

nav.piano {
    @include t(100%,100vh);
    @include xy-tl(60px,0);
    z-index: 80;
    background: $piano1;
    @include pianofont;
    &.active {
        transform: translateY(0%);
    }
    &.no-active {
        transform: translateY(-100%);
    }
    a {
        display: block;
        padding-top: 20px;
        margin-top: 20px;
        text-align: center;
        text-decoration: none;
        font-size: 30px;
        @include t(100%,60px);
        color: $piano2;
    }
}

//toxic

.menu.toxic {
    position: fixed;
    background: #fff;
    border-radius: 10px 10px 0 0;
    border-style: none;
    div {
        background: #333;
        position: relative;
        border-radius: 5px;
        &::after {
            @include pseudo(100%,100%);
            transform: translateY(12px);
            background: #333;
            border-radius: 5px;
        }
        &::before {
            @include pseudo(100%,100%);
            transform: translateY(-12px);
            background: #333;
            border-radius: 5px;
        }
    }
}

nav.toxic {
    position: fixed;
    z-index: 80;
    background: #fff;
    box-shadow: 0 0 5px $toxic2;
    a {
        display: block;
        padding-top: 20px;
        margin-top: 20px;
        text-align: center;
        text-decoration: none;
        font-size: 30px;
        @include t(100%,60px);
        color: $piano2;
    }
}

@media (orientation:portrait){
    .menu.toxic {
        @include t(50px,50px);
        border-radius: 0 10px 10px 0;
        box-shadow: 0 0 5px $toxic2;
        top: 15px;
        left: 0;
        div {
            @include t(40px,5px);
            margin-left: 5px;
        }
    }
    nav.toxic {
        @include t(100%,100vh);
        &.active {
            transform: translateX(0%);
        }
        &.no-active {
            transform: translateX(-100%);
        }
    }
}
@media (orientation:landscape){
    .menu.toxic {
        @include t(70px,70px);
        border-radius: 10px 10px 0 0;
        border-bottom: 1px solid #333;
        top: 35px;
        left: 35px;
        div {
            margin-left: 10px;
            @include t(50px,7px);
        }
    }
    nav.toxic {
        width: 70px;
        top: 35px;
        left: 35px;
        bottom: 35px;
        padding-top: 70px;
        border-radius: 10px;
    }
}

// neomorfismo 

#menu.neomorfismo {
    position: absolute;
    top: 15px;
    &::before {
        content: '';
        @include t(90%,5px);
        position: absolute;
        background: #888;
        top: 45%;
        left: 5%;
        z-index: -1;
    }
    button {
        @include t(60px,50px);
        background: $neomorfismo1;
        border-top: 5px solid #e9e9e9;
        border-left: 10px solid #e9e9e9;
        border-right: 10px solid #bbb;
        border-bottom: 2px solid #bbb;
        box-shadow: 3px 3px 3px rgba(100,100,100,0.2),
                    6px 6px 3px rgba(100,100,100,0.2),
                    9px 9px 3px rgba(100,100,100,0.2),
                    inset 1px 1px 1px #e3e3e3,
                    inset -1px -1px 1px #c9c9c9;
        @include centrar(h);
        div {
            @include t(70%,2px);
            left: 15%;
            background: $neomorfismo3;
            position: relative;
            &::after {
                @include pseudo(100%,100%);
                transform: translateY(8px);
                background: $neomorfismo3;
            }
            &::before {
                @include pseudo(100%,100%);
                transform: translateY(-8px);
                background: $neomorfismo3;
            }
        }
    }
}
@media (orientation:landscape){
    #menu.neomorfismo {
        width: 10%;
        right: 10%;
    }
}
@media (orientation:portrait){
    #menu.neomorfismo {
        width: 30%;
        right: 5%;
    }
}