@import "../mixins.scss";

//piano

section#seccion-2.piano {
    padding-bottom: 60px;
    padding-top: 60px;
    display: block;
    a.cont {
        text-decoration: none;
        width: 100%;
        position: relative;
        div.sub-cont {
            color: $piano1;
            @include centrar(vh);
            h3 {
                border-bottom: 5px solid $piano1;
                margin-bottom: 30px;
                font-size: 2em;
            }
        }
    }
}

@media (orientation:landscape) {
    section#seccion-2.piano {
        width: 80%;
        margin-left: 10%;
        a.cont {
            display: flex;
            img {
                width: 50%;
            }
            img.sc1 {
                margin-left: 50%;
            }
            div.sub-cont {
                @include t(50%,100%);
                position: absolute;
                top: 0;
            }
            div.sub-cont.sc0,
            div.sub-cont.sc2 {
                right: 0;
                padding: 0 0 0 60px;
                text-align: right;
            }
            div.sub-cont.sc1{
                left: 0;
                padding: 0 60px 0 0;
                text-align: left;
            }
        }
    }
}
@media (orientation:portrait) {
    section#seccion-2.piano {
        width: 90%;
        margin-left: 5%;
        a.cont {
            img {
                width: 100%;
                margin-top: 60px;
                margin-bottom: 60px;
            }
            div.sub-cont {
                width: 100%;
            }
        }
    }
}

//toxic 

section#seccion-2.toxic {
    display: block;
    a.cont {
        text-decoration: none;
        position: relative;
        img {
            width: 100%;
            margin-top: 60px;
            margin-bottom: 60px;
            border-radius: 10px;
            opacity: 0.8;
        }
        div.sub-cont {
            width: 100%;
            color: #fff;
            @include centrar(vh);
            h3 {
                margin-bottom: 30px;
                text-align: center;
                font-size: 3em;
            }
            p {
                text-align: center;
                font-size: 1.5em;
            }
            &:hover {
                text-shadow: 0 0 15px $toxic2;
                transition: 0.1s;
            }
        }
    }
}

@media (orientation:landscape) {
    section#seccion-2.toxic {
        width: 50%;
    }
}
@media (orientation:portrait) {
    section#seccion-2.toxic {
        width: 100%;
    }
}

//neomorfismo

section#seccion-2.neomorfismo {
    padding-bottom: 60px;
    padding-top: 60px;
    display: block;
    a.cont {
        text-decoration: none;
        width: 100%;
        position: relative;
        & span {
            position: absolute;
            @include t(50%,100%);
            border-radius: 10px;
            box-shadow: inset 0 0 20px rgba(0,0,0,0.7);
        }
        div.sub-cont {
            color: $neomorfismo3;
            @include centrar(vh);
            h3 {
                border-bottom: 5px solid $neomorfismo3;
                margin-bottom: 30px;
                font-size: 2em;
            }
        }
        img {
            border-radius: 10px;
        }
    }
}

@media (orientation:landscape) {
    section#seccion-2.neomorfismo {
        width: 80%;
        margin-left: 10%;
        a.cont {
            display: flex;
            img {
                width: 50%;
            }
            img.sc1 {
                margin-left: 50%;
            }
            img.sc1 ~ span {
                left: 50%;
            }
            div.sub-cont {
                @include t(50%,100%);
                position: absolute;
                top: 0;
            }
            div.sub-cont.sc0,
            div.sub-cont.sc2 {
                right: 0;
                padding: 0 0 0 60px;
                text-align: right;
            }
            div.sub-cont.sc1{
                left: 0;
                padding: 0 60px 0 0;
                text-align: left;
            }
        }
    }
}
@media (orientation:portrait) {
    section#seccion-2.neomorfismo {
        width: 90%;
        margin-left: 5%;
        a.cont {
            img {
                width: 100%;
                margin-top: 60px;
                margin-bottom: 60px;
            }
            div.sub-cont {
                width: 100%;
            }
            span {
                display: none;
            }
        }
    }
}