@import "componentes-especial/reloj.scss";
@import "componentes-especial/menu.scss";
@import "componentes-macro/header.scss";
@import "componentes-macro/section-1.scss";
@import "componentes-macro/section-1a.scss";
@import "componentes-macro/section-2.scss";
@import "mixins";

@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400;800&family=Rubik:wght@300;400;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@100;300&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    @include t(100%,100vh);
    overflow: hidden;
}

#reloj,header,.menu,nav,main {
    transition: none;
}

#root {
    @include t(100%,100vh);
    position: relative;
    transform-style: preserve-3d;
}

//piano

main {
    position: absolute;
    top: 0;
    left: 0;
    overflow: scroll;
    &::-webkit-scrollbar {
        display: none;
    }
}

main.piano {
    @include t(100%,100vh);
    background: #fff;
    @include pianofont;
}

//toxic

main.toxic {
    @include t(100%,100vh);
    background: $toxic1;
    @include toxicfont;
}

@media (orientation:landscape) {
    section.toxic {
        width: 50%;
        padding-left: 140px;
    }
}
@media (orientation:portrait){
    section.toxic {
        width: 100%;
        padding: 30px 7% 30px 7%;
    }
}

//neomorfismo 

main.neomorfismo {
    @include t(100%,100vh);
    background: $neomorfismo1;
    color: $neomorfismo3;
}