@import "../mixins.scss";

header {
    position: fixed;
    z-index: 80;
}

//piano
header.piano {
    @include t(100%,60px);
    background: $piano2;
    top: 0;
    left: 0;
    border-bottom: 2px solid $piano3;
    @include pianofont;
    @include centrar(h);
    h1 {
        position: absolute;
        font-size: 25px;
        width: 80%;
        left: 10%;
        &::after {
            content: 'Desarrollador web';
            position: absolute;
            right: 0;
        }
    }
}
@media (orientation:portrait) {
    header.piano {
        h1 {
            font-size: 20px;
            left: 5%;
            width: 90%;
            &::after {
                content: 'Front-end';
            }
        }
    }
}

//toxic

header.toxic {
    background: none;
    h1 {
        display: none;
    }
}

//neomorfismo

header.neomorfismo {
    background: $neomorfismo1;
    border-bottom: 2px solid #bbb;
    @include t(100%,90px);
    h1 {
        position: absolute;
        height: 50px;
        font-family: 'Stint Ultra Expanded', cursive;
        font-size: 1em;
        color: $neomorfismo3;
        top: 15px;
        background: $neomorfismo1;
        border-top: 5px solid #e9e9e9;
        border-left: 10px solid #e9e9e9;
        border-right: 10px solid #bbb;
        border-bottom: 2px solid #bbb;
        border-radius: 3px;
        box-shadow: 3px 3px 3px rgba(100,100,100,0.2),
                    6px 6px 3px rgba(100,100,100,0.2),
                    9px 9px 3px rgba(100,100,100,0.2),
                    inset 1px 1px 1px #e3e3e3,
                    inset -1px -1px 1px #c9c9c9;
        @include centrar(h);
        @include centrar(h);
        padding-left: 30px;
    }
}

@media (orientation:landscape){
    header.neomorfismo {
        h1 {
            width: 65%;
            left: 10%;
            &::after {
                content: '-desarrollador web.';
            }
        }
    }
}
@media (orientation:portrait){
    header.neomorfismo {
        h1 {
            width: 55%;
            left: 5%;
            &::after {
                content: '';
            }
        }
    }
}

@keyframes bateria {
    0%{width: 5%;}
    50%{width: 100%;}
    100%{width: 5%;}
}